import React from "react";
import { motion } from "framer-motion";

const animations = {
  initial: {
    opacity: 0,
    transform: "translateX(100%)",
  },
  animate: {
    opacity: 1,
    transform: "translateX(0)",
    transition: "0.3s ease",
  },
  exit: {
    opacity: 0, // Mettez l'opacit  0 pour l'animation de sortie
    transition: { duration: 0.3, ease: "easeOut" }, // Dfinissez la dure et l'ease appropris pour l'animation de sortie
    transform: "translateX(-100%)",
  },
};

const AnimatedPage = ({ children }) => {
  return (
    <motion.div
      key={children.key} // Assurez-vous que le composant est correctement mis  jour avec la cl
      variants={animations}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      {children}
    </motion.div>
  );
};

export default AnimatedPage;
